	module.exports = {"data":{"post":{"html":"<p><a href=\"http://www.spiegel.de/reise/metropolen/0,1518,309387,00.html\"><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2004/07/qm2welcome.jpg\" alt=\"QM2 welcomed in Hamburg Harbor (Spiegel Online)\" title=\"(DPA/SpOn) Queen Mary 2 im Hamburger Hafen\" align=\"right\" border=\"0\" /></a> The world's largest and most expensive passenger ship, the <a href=\"http://www.cunard.com/QM2/\">Queen Mary 2</a>, came to Hamburg for 24 hours, and it seems the entire city (plus a couple hundred thousand guests) played hooky on Monday to celebrate. School is on vacation, the weather was, well, acceptable (cloudy, cool, muggy, but dry), and it was a good excuse for a party. Hamburg has seen a lot a ships, but never anything like this. </p>\n<p>We (especially Christopher) had a hard weekend, so we skipped the celebration, settling for the view from the <em>Elbbrücken</em> on the way to work, the television footage, <a href=\"http://www.stern.de/lifestyle/reise/fernreisen/index.html?id=527075&nv=ct_rl&backref=%2Flifestyle%2Freise%2Ffernreisen%2Findex.html%3Fid%3D527125%26nv%3Dsb\">and</a> <a href=\"http://lys.antville.org/stories/859843/\">the</a> <a href=\"http://drinktank.blogg.de/index.php?cat=Icons\">dozens</a> <a href=\"http://www1.ndr.de/ndrde_slideshow/0,2964,OID472284_IMG475300_POS1_SIX0,00.html\">of</a> <a href=\"http://k-ho.de/slog/entry.php?id=00403\">pictures</a> <a href=\"http://www.hebig.com/queen_mary_2/\">online</a>.</p>","fields":{"title":"Royal Welcome","date":"19 Jul 2004","url":"https://papascott.de/2004-07-19-royal-welcome/"}}},"pathContext":{"slug":"/2004-07-19-royal-welcome/","prev":{"fields":{"title":"Outsourcing McDonald's drive-through order-taking","date":"19 Jul 2004","slug":"/2004-07-19-outsourcing-mcdonalds-drive-through-order-taking/"}},"next":{"fields":{"title":"No Redemption","date":"20 Jul 2004","slug":"/2004-07-20-no-redemption/"}}}}

